@import '/src/styles/variables.scss';

.header{
    margin-bottom: 29px;
    &__profession{
        margin: 102px 0 19px;

        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: $headingColor;
        cursor: default;
    }
    &__heading {
        margin-bottom: 29px;

        font-weight: 700;
        font-size: 45px;
        line-height: 55px;
        color: $headingColor;
    }
    &__burger {
        display: none;
    }
}

@media (max-width: 1024px) {
    .header {
        &__burger {
            display: flex;
            align-items: center;
            justify-content: center;

            border: 1px solid black;
            border-radius: 4px;
        }
    }
}